// Á±ªÁªÑ‰ª∂
class Home extends React.Componenet {
  constructor(props) {
    super(props);
    this.state = {
      num: 0
    }
  }

  // ÁîüÂëΩÂë®Êúü


  componentWillMount() {
    console.log('ÁªÑ‰ª∂Â∞ÜË¶ÅÂä†ËΩΩ');
  }

  componentWillReceiveProps(props) {
    console.log('props');
  }

  componentWillUpdate() {
    console.log('ÁªÑ‰ª∂Â∞ÜË¶ÅÊõ¥Êñ∞');
  }

  componentDidUpdate() {
    console.log('ÁªÑ‰ª∂Êõ¥Êñ∞ÂÆåÊàê');
  }

  componentDidMount() {
    console.log('ÁªÑ‰ª∂Âä†ËΩΩÂÆåÊàê');
  }
 
  handleClick(){
    console.log('handleClick')
    // ‰øÆÊîπÁä∂ÊÄÅ setState
    this.setState({
      num: this.state.num + 1
    })
  }

  render() {
    return (
      <div className="active">
        <h3 className="title">hello react</h3>
        <h3 className="title">{this.state.num}</h3>
        <button onClick={this.handleClick.bind(this)}>add</button>
      </div>
    );
  }
}

