import Componenet from "../react/component";

const ReactDOM = {
  render
}

function render(vnode, container) {
  const ele = _render(vnode);
  return container.appendChild(ele);
}

function createComponent(comp, props) {
  let inst;
  if (comp.prototype && comp.prototype.render) {
    // Á±ªÁªÑ‰ª∂
    inst = new comp(props);
  } else {
    // ÂáΩÊï∞ÁªÑ‰ª∂ËΩ¨Êç¢ÊàêÁ±ªÁªÑ‰ª∂
    inst = new Componenet(props);
    inst.constructor = comp;
    inst.render = function() { // ÊåÇËΩΩrenderÊñπÊ≥ï
      return this.constructor(props);
    }
  }

  return inst;
}

export function renderComponent(comp) {
  const renderer = comp.render();
  console.log('renderComponent', renderer);
  const base = _render(renderer);
  if (comp.base && comp.componentWillUpdate) {
    comp.componentWillUpdate();
  }
  if (comp.base && comp.componentDidUpdate) {
    comp.componentDidUpdate();
  } else if (comp.componentDidMount) {
    comp.componentDidMount();
  }

  // ËäÇÁÇπÊõøÊç¢
  if(comp.base && comp.base.parentNode) {
    comp.base.parentNode.replaceChild(base, comp.base);
  }


  comp.base = base;
  console.log('base', comp.base)
}

function setComponentProps(comp, props) {
  if (!comp.base) {
    if (comp.componentWillMount) comp.componentWillMount();
  } else if (comp.componentWillReceiveProps) {
    comp.componentWillReceiveProps(props);
  }
  // ËÆæÁΩÆÁªÑ‰ª∂Â±ûÊÄß
  comp.props = props
  // Ê∏≤ÊüìÁªÑ‰ª∂
  renderComponent(comp);
}

function _render(vnode) {
  console.log(vnode);
  if (vnode === undefined 
    || vnode === null
    || vnode === ''
  ) return;

    // Â¶ÇÊûúvnodeÊòØÊï∞ÂÄº
    if (typeof vnode === 'number') {
      vnode = String(vnode);
    }

  // Â¶ÇÊûúvnodeÊòØÂ≠óÁ¨¶‰∏≤
  if (typeof vnode === 'string') {
    return document.createTextNode(vnode);
  }

  const { tag, attrs, children } = vnode;

  // tagÊòØÂáΩÊï∞ÔºåÂàôÊ∏≤ÊüìÁªÑ‰ª∂
  if (typeof tag === 'function') {
    // ÂàõÂª∫ÁªÑ‰ª∂
    const comp = createComponent(tag, attrs);
    console.log('createComponent', comp)
    // ËÆæÁΩÆÁªÑ‰ª∂Â±ûÊÄß
    setComponentProps(comp, attrs);
    // ÁªÑ‰ª∂Ê∏≤ÊüìÁöÑËäÇÁÇπÂØπË±°ËøîÂõû
    return comp.base;
  }

  const dom = document.createElement(tag);

  if(attrs) {
    Object.keys(attrs).forEach(key => {
      setAttribute(dom, key, attrs[key]);
    })
  }

  // Ê∏≤ÊüìÂ≠êËäÇÁÇπ
  children && children.forEach(child => render(child, dom));


  return dom;
}

function setAttribute(dom, key, value) {
  if (key === 'className') {
    key = 'class';
  }

  // Â¶ÇÊûúÊòØ‰∫ã‰ª∂ onClick onBlur
  if (/on\w+/.test(key)) {
    key = key.toLowerCase();
  }

  if (key === 'style') {
    if (!value || typeof value === 'string') {
      dom.style.cssText = value || '';
    }

    if (value && typeof value === 'object') {
      for(let k in value) {
        if (typeof value[k] === 'number') {
          dom.style[k] = value[k] + 'px';
        } else {
          dom.style[k] = value[k];
        }
      }

    } else {
      if (key in dom) {
        dom[key] = value || '';
      }
      if (value) {
        dom.setAttribute(key, value);
      } else {
        dom.removeAttribute(key);
      }
    }
  }

  if (typeof value === 'function') {
    dom[key.toLowerCase()] = value;
  }

  
}

export default ReactDOM;
